export default [
    {
        path: "/",
        redirect: "/home"
    },
    {
        path: "/home",
        component: require('./components/Home/home.vue').default
    },
    {
        path: '/sobre-nosotros',
        component: require('./components/AboutUs/aboutUs.vue').default,
        name: 'sobre-nosotros'
    },
    {
        path: '/registro',
        component: require('./components/Register/register.vue').default,
        name: 'registro'
    },
    {
        path: '/all-products',
        component: require('./components/DisplayProducts/displayProducts.vue').default,
        name: 'all-products'
    },
    {
        path: '/galeria',
        component: require('./components/ProductGallery/productGallery.vue').default,
        name: 'galeria'

    },
    {
        path: '/cart',
        component: require('./components/ShoppingCart/shoppingCart').default,
        name: 'cart'

    }
];




