<template>
      <div id="top">
      <div class="container">
        <div class="col-md-6 offer" data-animate="fadeInDown">
          <a href="#" class="btn btn-success btn-sm" data-animate-hover="shake">Oferta Del Día</a>
          <a href="#">¡Obtén 35% de descuento en todas tus órdenes!</a>
        </div>
        <div class="col-md-6" data-animate="fadeInDown">
          <ul class="menu">
            <li>
              <a href="/registro">Inicia sesión</a>
            </li>
            <li>
              <a href="/registro">Regístrate</a>
            </li>
            <li>
              <a href="/sobre-nosotros">¿Quiénes Somos?</a>
            </li>
          </ul>
        </div>
      </div>
      <div class="modal fade" id="login-modal" tabindex="-1" role="dialog" aria-labelledby="Login" aria-hidden="true">
        <div class="modal-dialog modal-sm">

          <div class="modal-content">
            <div class="modal-header">
              <button type="button" class="close" data-dismiss="modal" aria-hidden="true">&times;</button>
              <h4 class="modal-title" id="Login">Inicio de Sesión</h4>
            </div>
            <div class="modal-body">
              <form>
                <div class="form-group">
                  <input type="text" class="form-control" id="email-modal" placeholder="Correo Electrónico">
                </div>
                <div class="form-group">
                  <input type="password" class="form-control" id="password-modal" placeholder="Contraseña">
                </div>

                <p class="text-center">
                  <button class="btn btn-primary">
                    <i class="fa fa-sign-in"></i> Iniciar Sesión</button>
                </p>

              </form>

              <p class="text-center text-muted">¿Aún no tienes una cuenta?</p>
              <p class="text-center text-muted">
                <a href="/registro">
                  <strong>¡Registrese Ahora!</strong>
                </a> ¡Es facil y puede hacerlo en 1 minuto para poder obtener descuentos especiales y mucho más!</p>

            </div>
          </div>
        </div>
      </div>

    </div>
</template>
<script>
 export default {
     name: 'top-bar'
 }
</script>

<style>

</style>



