<template>
    <div id="app">
        <top-bar></top-bar>
        <nav-bar></nav-bar>
        <router-view></router-view>

        <footer-bar></footer-bar>
    </div>
</template>

<script>
    import store from './store';
    export default {
        name: "app",
        data() {
            return {
                
            };
        },
        methods: {
            getLocalState() {
                store.commit('LOCAL_STATE', JSON.parse(localStorage.getItem('Store')));
            }
        },
        mounted() {
            this.getLocalState();
        }
    };
</script>

<style>
</style>


