import Vue from 'vue';
import Vuex from 'vuex';
import App from './App.vue';
import store from './store';
import VueRouter from 'vue-router';
import routes from './routes';
import VueCurrencyFilter from 'vue-currency-filter';
import sobreNosostros from './components/AboutUs/aboutUs.vue';

Vue.use(VueRouter);
Vue.use(Vuex);
Vue.use(VueCurrencyFilter, {
  symbol: 'RD$',
  thousandsSeparator: ',',
  fractionCount: 2,
  fractionSeparator: '.',
  symbolPosition: 'front',
  symbolSpacing: true
});

Vue.component('top-bar', require('./components/TopBar/topBar.vue').default);
Vue.component('nav-bar', require('./components/NavBar/navBar.vue').default);
Vue.component('home', require('./components/Home/home.vue').default);
Vue.component('footer-bar', require('./components/FooterBar/footerBar.vue').default);
Vue.component('sobre-nosotros', require('./components/AboutUs/aboutUs.vue').default);
Vue.component('registro', require('./components/Register/register.vue').default);
Vue.component('display-products', require('./components/DisplayProducts/displayProducts.vue').default);
Vue.component('cart', require('./components/ShoppingCart/shoppingCart.vue').default);


// Router Instance.
const router = new VueRouter({
  mode: 'history',
  routes: routes,
  linkActiveClass: 'router-link-active'
});

// Get AppStorage State from Browser
if (!localStorage.getItem('Store')) {
  localStorage.setItem('Store', JSON.stringify(store.state));
}

new Vue({
  el: '#app',
  router,
  store,
  components:{
    sobreNosostros
  },
  render: h => h(App)
})



