import store from '../../store';
export default {
    name: 'cart',
    data() {
        return {
            products: [],
            total: 0.00,
            suggestions:
                [
                    {
                        name: "iPhone X",
                        price: 70000,
                        hasDiscount: false,
                        newPrice: null,
                        isSale: false,
                        isNew: true,
                        isGift: false,
                        imgUrl: "iphonex",
                        imgUrl2: null
                    },
                    {
                        name: "Auriculares Bluetooth",
                        price: 4000,
                        hasDiscount: false,
                        newPrice: null,
                        isSale: false,
                        isNew: false,
                        isGift: true,
                        imgUrl: "auricularesbt",
                        imgUrl2: null
                    },
                    {
                        name: "Mouse Razer",
                        price: 4500,
                        hasDiscount: false,
                        newPrice: null,
                        isSale: false,
                        isNew: false,
                        isGift: true,
                        imgUrl: "razermouse",
                        imgUrl2: null
                    }
                ]
        };
    },
    computed: {
        shipping() {
            return this.total * 0.05;
        },
        itbis() {
            return this.total * 0.18;
        }
    },
    methods: {
        getLocalState() {
            store.commit("LOCAL_STATE", JSON.parse(localStorage.getItem("Store")));
            this.products = store.state.shoppingCart;
            this.getTotal();
        },
        getTotal() {
            this.total = 0;
            this.products.forEach(product => {
                if (product.hasDiscount) {
                    this.total += product.newPrice;
                }
                else {
                    this.total += product.price;
                }
            });
        },
        addToCart(product) {
            store.commit('ADD_PRODUCT', product);
            this.getLocalState();
        },
        getImgUrl(device) {
            var images = require.context('../../assets/', false, /\.jpg$/)
            return images('./' + device + ".jpg")
        },
        deleteProduct(index) {
            this.products.splice(index, 1);
            store.commit("DELETE_PRODUCT", this.products);
            this.getTotal();
        }
    },
    mounted() {
        this.getLocalState();
    }
}


