import * as types from './mutation-types';
import { state } from './index.js'

export const mutations = {
    [types.ADD_PRODUCT](state, payload) {
        var userConfig = JSON.parse(localStorage.getItem('Store'));
        userConfig.shoppingCart.push(payload);
        state.shoppingCart.push(payload);
         //Save State on Local Storage
         userConfig = JSON.stringify(userConfig);
         localStorage.setItem('Store', userConfig);
    },
    [types.LOCAL_STATE](state, payload) {
        state.shoppingCart = payload.shoppingCart;
    },
    [types.DELETE_PRODUCT](state, payload) {
        var userConfig = JSON.parse(localStorage.getItem('Store'));
        userConfig.shoppingCart=payload;
        state.shoppingCart=payload;
         //Save State on Local Storage
         userConfig = JSON.stringify(userConfig);
         localStorage.setItem('Store', userConfig);
    }
}


